<template>
  <div class="index news flex-content">
    <div class="header">
      <div class="header__left">
        <img class="header__logo" src="../assets/logo.png" alt="logo.png">
      </div>
      <div class="header__right">
        <div class="header__datetime">
          <span>{{ date }}</span> <span class="line"></span> <span>{{ time }}</span>
        </div>
      </div>
    </div>
    <div class="summary-box">
      <h1></h1>
      <div v-if="auth" class="index__hello">
         , {{ user.name }} {{ user.lastname }}
      </div>
      <div v-if="auth" class="index__today">
        <h2> </h2>
        <ul class="today-list">
          <li>
            <span class="today__unit bold-text">:</span>
            <span class="today__quantity">12 </span>
          </li>
          <li>
            <span class="today__unit bold-text">:</span>
            <span class="today__quantity">12 </span>
            <span class="today__now">  : </span>
            <span class="today__now">:</span>
          </li>
          <li>
            <span class="today__unit bold-text">:</span>
            <span class="today__quantity">12 </span>
            <span class="today__now">  : </span>
          </li>
          <li>
            <span class="today__unit bold-text">:</span>
            <span class="today__quantity">12 </span>
            <span class="today__now"> :</span>
          </li>
          <li>
            <span class="today__unit bold-text">:</span>
            <span class="today__quantity">12 </span>
            <span class="today__now">  : </span>
            <span class="today__now">:</span>
          </li>
        </ul>
      </div>
      <div v-if="!auth" class="news-all all">
        <div class="news-all__grid">
          <template v-for="item in news">
            <v-card class="news-single" :key="item.id" color="primary" v-if="item.photo_path"
                    @click="$router.push({name: 'NewOpen', params: {id: item.id}})"
                    v-bind:style="{'background-image': 'url('+$hostname+'media'+item.photo_path+ ')', 'background-size': 'cover', 'background-position': 'center'}">
              <div class="news-single__title">{{ item.title }}</div>
              <div class="news-single__text" v-html="item.text"></div>
            </v-card>
            <v-card class="news-single" :key="item.id" color="primary" v-else
                    @click="$router.push({name: 'NewOpen', params: {currentNew: item, id: item.id}})">
              <div class="news-single__title">{{ item.title }}</div>
              <div class="news-single__text" v-html="item.text"></div>
            </v-card>
          </template>
        </div>
      </div>
    </div>

    <v-btn v-if="!auth" class="action-btn" color="primary" @click="goLogin"></v-btn>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "Index",
  created() {
    console.log("init Index")
    if (localStorage.getItem('auth_token') || sessionStorage.getItem('auth_token')) {
      this.auth = true
    }
    this.loadData()
    this.refreshTime()
    setInterval(() => {
      this.refreshTime()
    }, 1000)
  },
  computed: {
    date: function () {
      let now = new Date()
      return now.getDate() + "." + (now.getMonth() + 1) + "." + now.getFullYear()
    },
  },
  data() {
    return {
      auth: false,
      news: {},
      user: {},
      time: ''
    }
  },
  methods: {
    refreshTime() {
      let now = new Date()
      let minutes = now.getMinutes()
      if (minutes < 10)
        minutes = "0" + minutes
      this.time = now.getHours() + ":" + minutes
    },
    goLogin() {
      this.$router.push({name: "Login"})
    },
    loadData() {
      if (this.auth)
        $.ajax({
          url: this.$hostname + "time-tracking/user",
          type: "GET",
          headers: {"Authorization": "Token " + (localStorage.getItem("auth_token") || sessionStorage.getItem("auth_token"))},
          success: (response) => {
            this.user = response.data.data
          },
          error: (response) => {
            if (response.status === 500) {
              this.alertMsg = "   "
            } else if (response.status === 401) {
              this.$refresh()
            } else {
              this.alertMsg = " "
            }
            this.alertError = true
          },
        })
      else
        $.ajax({
          url: this.$hostname + "time-tracking/news",
          type: "GET",
          success: (response) => {
            this.news = response.data.data
          },
          error: (response) => {
            this.alertError = true
            this.alertMsg = " "
            console.log(response.data)
          },
        })
    },
  }
}
</script>

<style scoped>

</style>